import {
  getFilteredProducts,
  getFilteredBlogs
} from "@/helpers/api-util";



async function handler(req, res) {
  if(req.method === "GET") {
    const blogResponse = await getFilteredBlogs();
    const productResponse = await getFilteredProducts();
    
    if(blogResponse) {
          try{
            res.status(201).json({blog: blogResponse})
          }catch(error){
            res.status(500).json({message: 'failure fetching Blog details'})
          } 
    }
     

    // if(productResponse){
    //    try {
    //      res.status(201).json({
    //        product: productResponse
    //      })

    //    } catch (error) {
    //      res.status(500).json({
    //        message: 'failure fetching Blog details'
    //      })
    //    }
    // }
    
  }
}

export default handler;
